.hidden {
  display: none;
}

.breed-select {
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
  border-radius: 10px;
  margin-bottom: 25px;
  padding: 10px;
  font-size: 20px;
}

body {
  background-image: linear-gradient(102.1deg, #60dd8e 8.7%, #188a8d 88.1%);
  font-family: Georgia, Times New Roman, Times, serif;
}

.cat-info {
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
  margin-top: 50px;
  display: flex;
}

.cat-description {
  width: 500px;
  text-align: center;
  border: double #0a0f07;
  border-radius: 50px;
  padding: 20px;
}

.cat-description:hover {
  cursor: pointer;
  scale: 1.05;
}

.Breed-message {
  margin: 0;
  padding: 0;
}

.blur-up {
  filter: blur(5px);
  transition: filter .4s;
}

.blur-up.lazyloaded {
  filter: blur();
}

img {
  border-style: ridge;
  border-radius: 25px;
  box-shadow: 1px 2px 10px #000;
}

img:hover {
  cursor: pointer;
  scale: 1.05;
}

.loader {
  height: 50px;
  width: 50px;
  animation: rotate 1s infinite;
  position: relative;
  top: 170px;
  left: 650px;
}

.loader:before, .loader:after {
  content: "";
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: block;
}

.loader:before {
  background-color: #fff;
  margin-bottom: 10px;
  animation: ball1 1s infinite;
  box-shadow: 30px 0 #60dd8e;
}

.loader:after {
  background-color: #60dd8e;
  animation: ball2 1s infinite;
  box-shadow: 30px 0 #fff;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg)scale(.8);
  }

  50% {
    transform: rotate(360deg)scale(1.2);
  }

  100% {
    transform: rotate(720deg)scale(.8);
  }
}

@keyframes ball1 {
  0% {
    box-shadow: 30px 0 #60dd8e;
  }

  50% {
    margin-bottom: 0;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #60dd8e;
  }

  100% {
    margin-bottom: 10px;
    box-shadow: 30px 0 #60dd8e;
  }
}

@keyframes ball2 {
  0% {
    box-shadow: 30px 0 #fff;
  }

  50% {
    margin-top: -20px;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #fff;
  }

  100% {
    margin-top: 0;
    box-shadow: 30px 0 #fff;
  }
}

